"use client";
import React, { useEffect, useState } from "react";
import SearchIC from "@/assets/svg/search";
import ArrowIC from "@/assets/svg/arrowdown";
import CancelIC from "@/assets/svg/cancel";
import MoreIC from "@/assets/svg/more";
import { Body2 } from "../Typography/Body.styles";
import { usePathname, useRouter } from "next/navigation";
import { H5 } from "../Typography/Heading.styles";
import SearchBar from "./Search/SearchBar";
import {
  Dropdown,
  DropdownItem,
  HeaderWrapper,
  MobileMenu,
  NavItem,
  NavList,
  NavOverlay,
} from "./Header.styles";

interface CateProps {
  id: number;
  tile: string;
  documentId: string;
}

const Header = () => {
  const [open, setOpened] = useState(false);
  const [isSearch, setIsSearch] = useState(false);

  // navbar mobile screen
  const [isNavOpen, setIsNavOpen] = useState<boolean>(false);

  const [cate, setCate] = useState<CateProps[]>([]);
  const router = useRouter();
  const pathname = usePathname();

  const handleGetCate = async () => {
    try {
      const response = await fetch(
        "http://localhost:1337/api/cates?populate=*"
      );
      const data = await response.json();
      setCate(data.data);
    } catch (error) {
      console.log(error);
    }
  };

  const handleSearchByCate = (id: string) => {
    router.push(`/${id}`);
  };

  useEffect(() => {
    handleGetCate();
  }, []);

  if (cate.length == 0) return null;

  return (
    <HeaderWrapper>
      {!isSearch ? (
        <>
          {/* Mobile menu */}
          <MobileMenu>
            <button onClick={() => setIsNavOpen(true)}>
              <MoreIC />
            </button>
            <button>
              <SearchIC />
            </button>
          </MobileMenu>

          {/* Overlay navbar */}
          {isNavOpen && (
            <NavOverlay>
              <div>
                <button onClick={() => setIsNavOpen(false)}>
                  <CancelIC />
                </button>
              </div>
            </NavOverlay>
          )}
          {/* Desktop menu */}
          <NavList>
            <NavItem>
              <a href="/">
                <H5 $size={18} $color={pathname === "/" ? "#EA8E31" : "#fff"}>
                  TRANG CHỦ
                </H5>
              </a>
            </NavItem>

            <NavItem>
              <a href="/about">
                <H5
                  $size={18}
                  $color={pathname === "/about" ? "#EA8E31" : "#fff"}
                >
                  GIỚI THIỆU
                </H5>
              </a>
            </NavItem>

            <NavItem onClick={() => setOpened(!open)}>
              <div
                style={{ display: "flex", gap: "12px", alignItems: "center" }}
              >
                <H5
                  $size={18}
                  $color={pathname.startsWith("/topic") ? "#EA8E31" : "#fff"}
                >
                  CHỦ ĐỀ
                </H5>
                <ArrowIC
                  fill={pathname.startsWith("/topic") ? "#EA8E31" : "#fff"}
                />
              </div>

              {open && (
                <Dropdown>
                  {cate.map((item, i) => (
                    <DropdownItem
                      key={i}
                      onClick={() => handleSearchByCate(item.documentId)}
                    >
                      <Body2 $color="#fff" $hoverColor="#EA8E31">
                        {item.tile}
                      </Body2>
                    </DropdownItem>
                  ))}
                </Dropdown>
              )}
            </NavItem>

            <NavItem>
              <a href="/FAQ">
                <H5
                  $size={18}
                  $color={pathname === "/FAQ" ? "#EA8E31" : "#fff"}
                >
                  HỎI ĐÁP
                </H5>
              </a>
            </NavItem>

            <NavItem onClick={() => setIsSearch(true)}>
              <SearchIC />
            </NavItem>
          </NavList>
        </>
      ) : (
        <SearchBar onCancel={() => setIsSearch(false)} />
      )}
    </HeaderWrapper>
  );
};

export default Header;
